"use client";
import { useState } from "react";
import { Menu, X } from "lucide-react";

export default function Navbar() {
    const [isOpen, setIsOpen] = useState(false);

    return (
        <nav style={{ display: "flex", justifyContent: "space-between", alignItems: "center", padding: "1rem", borderBottom: "1px solid #ccc", backgroundColor: "#fff", boxShadow: "0px 2px 5px rgba(0, 0, 0, 0.1)" }}>
            <div style={{ fontSize: "1.5rem", fontWeight: "bold", color: "#000", display: "flex", alignItems: "center" }}>
                <svg
                    role="img"
                    className="icon icon-LogoBetter2021"
                    width="8rem"
                    height="6rem"
                    viewBox="0 0 495 133"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <title>Better</title>
                    <path d="M0 1.967V129.574H60.1147C83.1991 129.574 101.331 115.441 101.331 93.9316C101.331 80.1929 92.5655 69.2516 79.8215 64.0711C91.3637 58.8906 99.1345 48.7367 99.1345 36.5936C99.1345 15.685 80.8161 1.94629 57.5245 1.94629H0V1.967Z" fill="currentColor"></path>
                </svg>
            </div>

            <button style={{ background: "none", border: "none", cursor: "pointer", display: "block", fontSize: "1.5rem" }} onClick={() => setIsOpen(!isOpen)}>
                {isOpen ? <X size={28} /> : <Menu size={28} />}
            </button>

            <ul style={{ display: isOpen ? "block" : "flex", flexDirection: isOpen ? "column" : "row", position: isOpen ? "absolute" : "static", top: "3rem", left: 0, width: "100%", backgroundColor: "#fff", boxShadow: isOpen ? "0px 2px 5px rgba(0, 0, 0, 0.1)" : "none", padding: "1rem", listStyle: "none", gap: "2rem", justifyContent: "center", alignItems: "center" }}>
                <li style={{ padding: "0.5rem" }}><a href="/" style={{ textDecoration: "none", color: "#000", fontSize: "1rem", fontWeight: "500" }}>Home</a></li>
                {["About-Us", "Mortgage-Calculator", "Start"].map((text) => (
                    <li key={text} style={{ padding: "0.5rem" }}>
                        <a href={`/${text.toLowerCase()}`} style={{ textDecoration: "none", color: "#000", fontSize: "1rem", fontWeight: "500" }}>{text}</a>
                    </li>
                ))}
            </ul>

            <a href="/signin" style={{ textDecoration: "none", fontSize: "1.5rem", fontWeight: "900", color: "#000", display: "block" }}>
                Sign in
            </a>
        </nav>
    );
}
